import { useState } from "react";

const Calculator = () => {
  const [display, setDisplay] = useState("0");

  return (
    <>
      <h2>Calculator</h2>

      <div id="container">
        <div id="display">{display}</div>
        <div id="top-col">
          <div>4</div>
        </div>
        <div id="sec-col">
          <div>7</div>
          <div>8</div>
          <div>9</div>
          <div>X</div>
        </div>
        <div id="thi-col">
          <div>7</div>
          <div>8</div>
          <div>9</div>
          <div>X</div>
        </div>
        <div id="fou-col">
          <div>7</div>
          <div>8</div>
          <div>9</div>
          <div>X</div>
        </div>
        <div id="fiv-col">
          <div>7</div>
          <div>8</div>
          <div>9</div>
          <div>X</div>
        </div>{" "}
        <div id="bot-col">
          <div>7</div>
          <div>8</div>
          <div>9</div>
          <div>X</div>
        </div>
      </div>

      <style jsx>{`
        #container {
          box-sizing: border-box;
          max-width: 600px;
          height: 750px;
          margin: 0 auto;
          border: 1px solid black;
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          grid-template-rows: repeat(8, 1fr);
        }
        #display {
          width: 100%;
          grid-column: 1 / 5;
          grid-row: 1 / 3;
          background-color: red;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          font-size: 5rem;
        }
        #top-col {
          grid-column: 1 / 5;
          display: flex;
          background-color: grey;
        }
        #sec-col {
          grid-column: 1 / 5;
          background-color: red;
        }
        #thi-col {
          grid-column: 1 / 5;
          background-color: green;
        }
        #fou-col {
          grid-column: 1 / 5;
          background-color: green;
        }
        #fiv-col {
          grid-column: 1 / 5;
          background-color: green;
        }
        #bot-col {
          grid-column: 1 / 5;
          background-color: blue;
        }
        div {
            border: 1px solid blue;
        }
      `}</style>
    </>
  );
};

export default Calculator;
